import React, { Component } from "react"
import blogClient from "../../hexagons/clients/blog"
import ForceDirectedGraph from "../../lib/ForceDirectedGraph/ForceDirectedGraph"
import Point2D from "../../lib/ForceDirectedGraph/Point2D"

const PALETTE = [
  "#a00",
  "#0a0",
  "#00a",
  "#990",
  "#da0",
  "#099",
  "#909",
  "#555",
  "#f3f",
]
const TWO_PI = 2 * Math.PI

class ForceDirectedPosts extends Component {
  componentDidMount() {
    blogClient.Post.list().then(response => {
      const items = response.data().items
      this.setup(items)
    })
  }

  setup = items => {
    this.fdg = new ForceDirectedGraph(new Point2D(TWO_PI, TWO_PI))

    this.fdg.randomizePositions(
      items.map(item =>
        item.tags === [] ? { ...item, tags: ["untagged"] } : item
      )
    )
    this.fdg.prepareRelationshipMatrix()

    this.step()
  }

  counter = 0

  step = () => {
    this.counter = (this.counter + 1) % 200

    if (this.counter === 0) {
      this.fdg.placedItems.forEach(item => {
        item.position.x += 0.5 * (Math.random() - 0.5)
        item.position.y += 0.5 * (Math.random() - 0.5)
      })
    }
    this.fdg.step()

    this.ctx.clearRect(0, 0, 500, 500)

    this.fdg.placedItems.forEach(item => {
      const pos = new Point2D(item.position.x, item.position.y)
      pos.scale(500 / TWO_PI)

      let step = TWO_PI / item.tags.length

      item.tags.forEach((tag, i) => {
        this.ctx.beginPath()
        this.ctx.moveTo(pos.x, pos.y)
        this.ctx.arc(pos.x, pos.y, 8, i * step, (i + 1) * step)
        this.ctx.fillStyle = PALETTE[tag.charCodeAt(0) % PALETTE.length]
        this.ctx.fill()
      })
    })

    setTimeout(this.step, 1)
  }

  render() {
    return (
      <canvas
        width="500"
        height="500"
        ref={ref => (this.ctx = ref.getContext("2d"))}
      />
    )
  }
}

export default ForceDirectedPosts
